import React from 'react'
import {Grid, Cell} from 'react-mdl'
export const aboutpage = () => {
    return (
        <div style={{ color:'white',background: 'url(https://imgcy.trivago.com/c_limit,d_dummy.jpeg,f_auto,h_1300,q_auto,w_2000/itemimages/16/07/160773_v8.jpeg) center / cover'}} >
                <h1><center>About Us</center></h1>
                <Grid>

                </Grid>
                <Grid className="demo-grid-3">
                    <Cell col={2} phone={4}>
                    <center>
                    <h1>visitour</h1>
                    <h4>Visitour is here to bring you all the amazing places the world you haven't seen, and have seen,
                        right from your screen. 
                        <br/> 
                        See amazing pictures, get travel advice, and share your experiences with everyone!
                    </h4>
                    </center>
                    </Cell>
                    <Cell col={4} tablet={6}>
                        <img src="https://blogs-images.forbes.com/erikkain/files/2017/11/aco.jpg" alt="img" style={{width:'100%'}}/>
                    </Cell>
                    <Cell col={6} tablet={8}>
                        <img src="https://www.fodors.com/assets/destinations/705529/eiffel-tower-paris-france_main.jpg" alt="img"style={{width:'100%'}}/>
                    </Cell>
                </Grid>
        </div>
    )
}

export default aboutpage;