import React from 'react';
import {Switch, Route } from 'react-router-dom';
import Home from './home'
import About from './aboutpage';


const Main = () => (
    
    <Switch>
        <Route exact path="/home" component={Home} />
        <Route exact path="/aboutpage" component={About} />
        <Route exact path="/aboutpage" component={About} />

    </Switch>
)

export default Main;